import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { AvisoPrevioDias, CLASS_NAME } from '../../../../models/dtos/mfp/avisoPrevioDias';

interface AvisoPrevioDiastEditViewProps extends EditViewProps<AvisoPrevioDias> {}

const AvisoPrevioDiasEditView: React.FC<AvisoPrevioDiastEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<any>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  return (
    <WindowComponent title="Dias de Aviso Prévio">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              id="anos"
              label="Até (anos)"
              isPrependLabel={true}
              placeholder="Até (anos)"
              formProps={{
                ...formProps,
                validation: {
                  min: 0,
                  required: true,
                },
              }}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="diasAviso"
              label="Dias de Aviso"
              isPrependLabel={true}
              placeholder="Dias de Aviso"
              formProps={{
                ...formProps,
                validation: {
                  min: 0,
                  required: true,
                },
              }}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default AvisoPrevioDiasEditView;
